.updatebtn {
    display: flex;
    justify-content: center;
    float: right;
}

.row-gap-15 {
    row-gap: 15px;
    place-content: space-between;
    place-items: center;
}

.col-gap-15 {
    column-gap: 15px;
    display: flex;
    justify-content: flex-end;

}

.register-cont {
    min-height: unset;
}

.PhoneInputInternationalIconPhone {
    fill: #d8d2d2a1;
}

.PhoneInputInput {
    outline: none;
    border: none;
}

.PhoneInput--focus {
    color: #768192;
    background-color: #fff;
    border-color: #958bef;
    outline: 0;
    box-shadow: 0 0 0 0.2rem rgb(50 31 219 / 25%);
}

.input-group-text {

    min-width: 40px;
    place-content: center;
}

.badg-click {
    cursor: pointer;
}

.servicesTabel {
    .pagination {
        padding-left: 0;
        flex-flow: wrap;
    }

    .table th,
    .table td {
        padding: 0.55rem;
        vertical-align: middle;
        border-top: 1px solid;
        text-align: center;
        border-top-color: #d8dbe0;
    }
}

.arabic-align {

    direction: rtl;
    text-align: start;

}

.switch_label {
    display: flex;
    row-gap: 15px;
    column-gap: 10px;
    flex-flow: wrap;

    .sw-amount {
        position: relative;

        height: 20px;

    }

    .c-switch-info .c-switch-input:checked+.c-switch-slider::before {
        border-color: #0080ff;
        height: 15px;
        width: 15px;
        left: 6px;
        top: 2px;
    }

    .c-switch-pill .c-switch-slider::before {
        border-color: #0080ff;
        height: 15px;
        width: 15px;
        left: 3px;
        top: 2px;

    }
}

.imgLogo {
    width: 53%;
    margin: 0 auto;
    position: relative;
    display: block;
    min-width: 185px;
}

.react-time-picker__wrapper {
    direction: ltr;
    display: flex;
    flex-grow: 0;
    border-radius: 3px;
    padding: 3px 14px;
    flex-shrink: 0;
    border: 1px solid #d8dbe0;
}

@media (max-width:768px) {
    .col-gap-15 {
        flex-flow: wrap;

    }

}